<template>
  <div class="departamento-user-profile container">
    <v-container>
      <v-row>
        <v-col>
          <NavigationBreadcrumbs />
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col>
          <div class="departamento-user-profile__inner">
            <v-container>
              <v-row>
                <v-col sm="12" md="2" lg="4">
                    <div class="departamento-user-profile__avatar">
                      <img src="@/assets/images/profile-photo.jpg" />
                    </div>
                    <div class="departamento-user-profile__info">
                      <div class="departamento-user-profile__info-full-name">
                        <base-editable-text
                          v-model="testUser.fullName"
                          @enter="handleInputValueSave"
                        ></base-editable-text>
                      </div>
                      <div class="departamento-user-profile__info-regalia">
                        <base-editable-text
                          inputType="textarea"
                          v-model="testUser.regalia"
                          @enter="handleInputValueSave"
                        ></base-editable-text>
                      </div>
                      <div class="departamento-user-profile__info-actions">
                        <div class="departamento-user-profile__button departamento-user-profile__button__register">
                          <button>
                            <span>
                              <v-icon>
                                mdi-account-plus
                              </v-icon>
                            </span>
                            <span>
                              Зареєструвати користувача
                            </span>
                          </button>
                        </div>
                        <div class="departamento-user-profile__button departamento-user-profile__button__delete">
                          <button>
                            <span>
                              <v-icon>
                                mdi-account-off
                              </v-icon>
                            </span>
                            <span>
                              Видалити користувача
                            </span>
                          </button>
                        </div>
                      </div>
                    </div>
                </v-col>
                <v-col sm="12" md="10" lg="8">
                  <div class="departamento-user-profile__main">
                    <div class="departamento-user-profile__tabs">
                      <Tabs
                        :activeTab="activeTab"

                        :tabs="[
                          { name: 'Усі документи', componentName: 'all' },
                          { name: 'Індивідуальній план', componentName: 'indiv-plan-docs' },
                          { name: 'Робочі програми', componentName: 'working-plan-docs' },
                        ]"

                        @tab-click="handleTabClick"
                      />
                    </div>
                    <div class="departamento-user-profile__documents">
                      <UserDocList 
                        :documents="docs"
                      />
                    </div>
                  </div>
                </v-col>
              </v-row>
            </v-container>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import BaseCard from "@/components/BaseComponents/BaseCard";
import BaseEditableText from '@/components/BaseComponents/BaseEditableText';

import UserDocList from '@/components/UserDocList';

import NavigationBreadcrumbs from "@/elements/NavigationBreadcrumbs";
import Tabs from "@/elements/Tabs";

export default {
  name: "UserProfile",
  components: {
    BaseCard,
    NavigationBreadcrumbs,
    Tabs,
    UserDocList,
    BaseEditableText
  },
  data() {
    return {
      testUser: {
        fullName: 'Іванов Іван Іванович',
        regalia: 'Доктор економічних наук, кандидат технічних наук, професор, завідувач кафедри прикладних інформаційних систем.'
      },
      activeTab: 'all'
    }
  },
  mounted() {
    if (this.docs.length === 0) {
      this.fetchFakeDocuments();
    }
  },
  methods: {
    handleInputValueSave() {
      console.log('call save method to server with updated data')
    },
    handleTabClick(componentName) {
      this.activeTab = componentName;
    },
    fetchFakeDocuments() {
      this.$store.dispatch('userProfile/fetchDocs')
        .catch(err => {
          console.error('Error in fetching docs!', err);
        })
    },
  },
  computed: {
    docs() {
      return this.$store.state.userProfile.documents
    }
  }
};
</script>
