<template>
  <div class="user-doc-list">
    <div class="user-doc-list__inner">
      <transition name="fade-out" mode="out-in">
        <div key="loadingState" class="user-doc-list__spinner" v-if="isLoading">
          <ScalingSquaresSpinner
            :animation-duration="1000"
            :size="36"
            color="#3E68B7"
           />
        </div>

        <div key="docList" class="user-doc-list__docs" v-else>
          <button 
              class="user-doc-list__docs-item"
              v-for="doc in documents"
              :key="doc.uid"
          >
            <div class="user-doc-list__docs-item__title">
              <p>
                {{ formatDocTitle(doc.title) }}
              </p>
            </div>
            <div class="user-doc-list__docs-item__date">
              <p>
                {{ doc.date }}
              </p>
            </div>
          </button>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
import { ScalingSquaresSpinner } from 'epic-spinners';

export default {
  name: 'UserDocList',
  components: {
    ScalingSquaresSpinner,
  },
  props: {
    documents: {
      required: false,
      default: () => []
    },
  },
  methods: {
    formatDocTitle(docTitle) {
      return docTitle.length > 40 
        ? docTitle.slice(0, 37) + '...'
        : docTitle
    }
  },
  computed: {
    isLoading() {
      return this.documents.length === 0 
    }
  }
}
</script>