<template>
  <div class="base-editable-text">
    <transition name="fade-out" mode="out-in">
      <div 
        class="base-editable-text__data" 
        v-if="!isEditing"
        @click="isEditing = true"
      >
        <p>
          {{ value }}
        </p>
      </div>
      <div class="base-editable-text__input" v-else>
        <FormulateInput 
          :type="inputType"
          :value="value"

          @input="$emit('input', $event)" 
          @keydown.enter="handleSaveInputValue"
        />
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  name: 'EditableText',
  props: {
    value: null,
    inputType: {
      type: String,
      default: 'text',
    }
  },
  data() {
    return {
      isEditing: false,
    }
  },
  methods: {
    handleSaveInputValue(e) {
      this.isEditing = false;
      this.$emit('enter', e.currentTarget.value);
    },
  }
}
</script>